@font-face {
  font-family: 'Roboto';
  src: url(../fonts/Roboto-Regular.woff2), url(../fonts/Roboto-Regular.woff);
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Roboto';
  src: url(../fonts/Roboto-Bold.woff2), url(../fonts/Roboto-Bold.woff2);
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul,
ol,
dl {
  list-style: none;
}
img {
  max-width: 100%;
}
.page__body {
  max-width: 100%;
  min-width: 320px;
  font-family: 'Roboto', 'Arial', sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #424242;
  background-color: #EDE8E2;
  overflow-x: hidden;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  max-width: 1740px;
  padding: 0 17px;
}
@media (min-width: 744px) {
  .container {
    padding: 0 61px;
  }
}
@media (min-width: 1400px) {
  .container {
    padding: 0 117px;
  }
}
@media (min-width: 1920px) {
  .container {
    margin: 0 auto;
    padding: 0;
  }
}
.main__caption {
  font-size: 24px;
  font-weight: 700;
  line-height: 28px;
}
.main__caption span {
  color: #8B9D83;
}
@media (min-width: 1400px) {
  .main__caption {
    font-size: 36px;
    line-height: 42px;
  }
}
@media (min-width: 1920px) {
