    font-size: 48px;
    line-height: 56px;
  }
}
.foreword {
  width: 283px;
}
.foreword h2 {
  margin-bottom: 20px;
}
@media (min-width: 744px) {
  .foreword {
    display: flex;
    align-items: center;
    gap: 40px;
    width: 100%;
  }
  .foreword h2 {
    margin: 0;
  }
  .foreword p {
    width: 80%;
  }
}
@media (min-width: 1400px) {
  .foreword {
    gap: 200px;
  }
