    gap: 290px;
  }
  .foreword p {
    font-size: 28px;
    line-height: 32px;
  }
}
.primary-button {
  display: block;
  width: 150px;
  padding: 17px 0;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  background-color: #8B9D83;
  border: none;
  border-radius: 58px;
  cursor: pointer;
}
.primary-button:hover {
  background-color: #6D8E5E;
}
.primary-button:active {
  background-color: #8B9D83;
}
.primary-button:disabled {
  background-color: #dcdcdc;
  cursor: default;
}
.primary-button--arrow {
  position: relative;
  padding-right: 35px;
}
.primary-button--arrow::before {
  content: "";
  position: absolute;
  top: calc(50% - 7.5px);
  right: 18px;
  width: 17px;
  height: 15px;
  background: url('../img/icon-arrow-right.svg') no-repeat 50%;
}
.secondary-button {
  display: block;
  min-width: 150px;
  padding: 15px 0;
  font-size: 16px;
  line-height: 18px;
  text-align: center;
  color: #686656;
  background-color: none;
  border: 2px solid #8B9D83;
  border-radius: 55px;
  cursor: pointer;
}
.secondary-button:hover {
  border-color: #6D8E5E;
}
.secondary-button:active {
  border-color: #8B9D83;
}
.secondary-button:disabled {
  border-color: #dcdcdc;
  cursor: default;
}
.input {
  appearance: none;
  height: 45px;
  padding: 15px;
  font-style: inherit;
  color: #515151;
  border: 2px solid #D5D5D5;
  border-radius: 17px;
}
.input:hover,
.input:focus {
  border-color: #6D8E5E;
}
.input:focus-visible {
  outline: none;
}
