.header__phone {
  margin-left: auto;
  z-index: 5;
}
.header__button {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 28px;
  height: 16px;
  border: none;
  background: none;
  cursor: pointer;
}
.header__button:hover .header__burger {
  background-color: #6D8E5E;
}
.header__button:active .header__burger {
  background-color: #8B9D83;
}
@media (min-width: 744px) {
  .header__button {
    width: 49px;
    height: 30px;
  }
}
@media (min-width: calc(1400px - 200px)) {
  .header__button {
    display: none;
  }
}
.header__burger {
  width: 100%;
  height: 2px;
  background-color: #424242;
  transition: all 0.3s ease;
}
.header__burger--third {
  width: 50%;
}
@media (min-width: 744px) {
  .header__burger {
    height: 3px;
  }
}
.header__button--cross .header__burger--first {
  transform: translateY(7px) rotate(45deg);
}
@media (min-width: 744px) {
  .header__button--cross .header__burger--first {
    transform: translateY(14px) rotate(45deg);
  }
}
.header__button--cross .header__burger--second {
  transform: rotate(-45deg);
}
.header__button--cross .header__burger--third {
  width: 50%;
  opacity: 0;
}
.header__phone-link {
  display: none;
}
@media (min-width: calc(1400px - 200px)) {
  .header__phone-link {
    position: relative;
    display: initial;
    padding-left: 43px;
    font-size: 20px;
    text-decoration: none;
    color: #424242;
  }
  .header__phone-link:hover {
    color: #6D8E5E;
  }
  .header__phone-link:active {
    color: #8B9D83;
  }
  .header__phone-link::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 25px;
    height: 24px;
    background: url('../img/icon-phone.svg') no-repeat center;
  }
}
@media (min-width: 1920px) {
  .header__phone-link {
    font-size: 24px;
    line-height: 28px;
  }
}
.header__popup {
  position: absolute;
  top: 0;
  right: 0;
  width: 320px;
  height: 640px;
  background-color: #EDE8E2;
  overflow: hidden;
  z-index: 1;
  transform: translateY(-100%);
  transition: all 0.4s ease-out;
}
.header__popup--active {
  transform: translateY(0);
  transition: all 0.4s ease-in;
}
@media (min-width: calc(1400px - 200px)) {
  .header__popup {
    display: none;
  }
}
